import logo from './logo.svg';
import './App.css';
import 'font-awesome/css/font-awesome.min.css';
import {
  BrowserRouter as Router,
  HashRouter,
  Switch,
  Route,
  Link
} from "react-router-dom";
import GenericNotFound from './components/page/notfound';
import Home from './components/page/home';
import About from './components/page/about';
import Login from './components/auth/login';
import Register from './components/auth/register';
import Dashboard from './components/page/dashboard';
import Viewpost from './components/page/viewpost';

function App() {
  const sta={
    color: 'white',
  }

  const footer={
    height: '50px',
    position: 'fixed',
    bottom: '0'
  }

  return (
    <div className="App">
      <nav className="navbar navbar-expand-sm bg-dark navbar-dark">
        <div className="container">
          <a className="navbar-brand" href="#/">React Blog</a>
          <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
            <span className="navbar-toggler-icon"></span>
          </button>

          <div className="collapse navbar-collapse" id="navbarSupportedContent">
          <ul className="navbar-nav mr-auto">
          {localStorage.getItem('authlogin')==='false' && (<li className="nav-item">
                  <a className="nav-link" href="#/">Home</a>
              </li>)}
              {localStorage.getItem('authlogin')==='true' && (<li className="nav-item">
                  <a className="nav-link" href="#/dashboard">Home</a>
              </li>)}
              <li className="nav-item">
                  <a className="nav-link" href="#/about">About</a>
              </li>
          </ul>
          <ul className="navbar-nav ml-auto">
          {localStorage.getItem('authlogin')==='false' && (<li className="nav-item">
                  <a className="nav-link" href="#/register">Register</a>
              </li>)}
              {localStorage.getItem('authlogin')==='false' && (
              <li className="nav-item">
                  <a className="nav-link" href="#/login">Login</a>
              </li>)}
              {localStorage.getItem('authlogin')==='true' && (
                <li className="nav-item dropdown">
                <a className="nav-link dropdown-toggle" href="#" id="navbarDropdownMenuLink" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                {localStorage.getItem('user_id')}
                </a>
                <div className="dropdown-menu bg-dark" aria-labelledby="navbarDropdownMenuLink">
                  <a className="nav-link bg-dark" href="#/logout">Logout</a>
                </div>
              </li>)}
          </ul>
          </div>
          </div>
      </nav>
      <HashRouter basename="/" >
      <Switch>
          <Route exact path={"/"} component={Home} ></Route>
          <Route exact path={"/about"} component={About} ></Route>
          <Route exact path={"/login"} component={Login} ></Route>
          <Route exact path={"/register"} component={Register} ></Route>
          <Route exact path={"/dashboard"} component={Dashboard} ></Route>
          <Route exact path={"/viewpost/:id"} component={Viewpost} ></Route>
          <Route exact path={"/logout"} component={Logout} ></Route>
          <Route path="/" component={GenericNotFound} />
      </Switch>
      </HashRouter>
      <div className="container-fluid" style={footer}>
        <div className="row">
          <div className="mx-auto" style={sta}>&copy; 2021 - React Blog</div>
        </div>
      </div>
    </div>
  );
  function Logout() {
    localStorage.setItem('authlogin', 'false');
    window.location.href = "/reactblog/";
    return (<div></div>);
  }
  /*function Home() {
    return (
      <div className="container">
        <h1>Home</h1>
      </div>
    );
  }*/
}

export default App;
