import React,{ useRef,useState,useEffect } from 'react';

import firebase from './../../firebase.js';
import $ from 'jquery';
import moment from 'moment';

const register = function Register() {
  const name = useRef(null);
  const nickname = useRef(null);
  const email = useRef(null);
  const password = useRef(null);
  const [users,setUsers] = useState([]);
  const [userscount,setUserscount] = useState(0);

  const bgstyle={
    backgroundColor: '#282c34',
    color: 'white'
  }

  const registerform={
    borderRadius: '25px',
    backgroundColor: 'lightblue'
  }

  const button={
    borderRadius: '15px',
    margin: '0 0 15px 0',
  }

  const fontcolor={
    color: 'blue'
  }

  function register(){
    //alert("Register: "+JSON.stringify(name)+JSON.stringify(nickname)+JSON.stringify(email)+JSON.stringify(password));
    //alert("Register: "+name.current.value+nickname.current.value+email.current.value+password.current.value);
    const auth = firebase.auth();
      try{
        auth.createUserWithEmailAndPassword(email.current.value,password.current.value)
          .then(() => { 
            //setTimeout(function(){
            //try{
              writeMessageToDB();
            //}catch(err){
              //setTimeout(function(){ 
              //alert("Error : ", err);
              //}.bind(this), 3000);
            //}
            //}.bind(this), 10000);
            //window.location.href = "/reactchat/login"; 
          })
          .catch(error => {   
            switch(error.code) {
              case 'auth/email-already-in-use':
                    alert('Email already in use !');
                    break;
              default:
                break;
          }
        });
      }catch(err){
          alert("Error : ", err);
      }
  }

  useEffect(() => {
    firebase.database().ref("/users").orderByChild('email')
      .on('value', snapshot => {
          let userg = [];
          snapshot.forEach(child => {
          var data = child.val();
          userg.push({id: data.id,email: data.email,name: data.name,nickname: data.nickname,password: data.password,phone: data.phone,role: data.role,status: data.status,terms: data.terms,time: data.time});
          });
          //setState({users: userg,userscount: userg.length})
          setUsers(userg);
          setUserscount(userg.length);
          //alert(this.state.userscount)
      });
    $(".container-fluid").css("position","fixed");
  })

  function writeMessageToDB() {
    var curtime = moment().format("DD/MM/YYYY HH:mm:ss");
    var nametemp = name.current.value;
    var emailtemp = email.current.value;
    var nicknametemp = nickname.current.value;
    var phone = '0777888999';
    var role = 'demo';
    //alert(curtime);
    //alert(name);
    //alert(email);
    //alert(nickname);
    //alert(phone);
    //let data = ({name: name,email: email,nickname: nickname,phone: phone,time: curtime });
    //alert(JSON.stringify(data));
    //alert(JSON.stringify(firebase.database().ref("/usersdetails/a")));
    //setTimeout(function(){ 
    //var ddbbref;
    //var dbref = firebase.database().ref('/users');
    
    //dbref.push({
    //    'name': name,
    //    'email': email,
    //    'nickname': nickname,
    //    'phone': phone,
    //    'time': curtime,
    //    'role': role
    //  }).set();
    /*
    firebase.database().ref('/users').push({
        'name': name,
        'email': email,
        'nickname': nickname,
        'phone': phone,
        'time': curtime,
        'role': role,
        'terms':0,
        'password': this.password.current.value
      }).set();*/
      var refdb = firebase.database().ref('/users').push();
      refdb = refdb.toString().slice(-20);
      //alert(JSON.stringify(refdb));
      //alert(refdb);
      //alert(this.state.userscount);
      //setTimeout(function(){ 
      let updateData = firebase.database().ref("/users").child(refdb);
      updateData.child("id").set(userscount+1);
      updateData.child("name").set(nametemp);
      updateData.child("email").set(emailtemp);
      updateData.child("nickname").set(nicknametemp);
      updateData.child("phone").set(phone);
      updateData.child("time").set(curtime);
      updateData.child("role").set(role);
      updateData.child("status").set("active");
      updateData.child("terms").set(0);
      updateData.child("password").set(password.current.value);
    
      //alert(JSON.stringify(ddbbref));
      //}.bind(this), 3000);
      window.location.href = "/reactblog/";
      //}, 300);
  }

  return (
      <div className="container" style={bgstyle}>
        <h2>This is Register Page</h2>
        <div className="card col-6 was-validated mx-auto" style={registerform}>
          <h1 className="h3 mb-3 font-weight-normal" style={fontcolor}>Please sign up</h1>
          <div class="form-group">
          <label htmlFor="name" className="sr-only">User Name</label>
          <input ref={name} type="text" id="name" className="form-control" placeholder="User Name" required autofocus />
          <div class="valid-feedback"></div>
          <div class="invalid-feedback">Please fill out this field.</div>
          </div>
          <div class="form-group">
          <label htmlFor="nickname" className="sr-only">Nick Name</label>
          <input ref={nickname} type="text" id="nickname" className="form-control" placeholder="Nick Name" required autofocus />
          <div class="valid-feedback"></div>
          <div class="invalid-feedback">Please fill out this field.</div>
          </div>
          <div class="form-group">
          <label htmlFor="inputEmail" className="sr-only">Email address</label>
          <input ref={email} type="email" id="inputEmail" className="form-control" placeholder="Email address" required autofocus />
          <div class="valid-feedback"></div>
          <div class="invalid-feedback">Please fill out this field.</div>
          </div>
          <div class="form-group">
          <label htmlFor="inputPassword" className="sr-only">Password</label>
          <input ref={password} type="password" id="inputPassword" className="form-control" placeholder="Password" required />
          <div class="valid-feedback"></div>
          <div class="invalid-feedback">Please fill out this field.</div>
          </div>
          <div class="form-group">
          <label htmlFor="inputConfirmPassword" className="sr-only">Confirm Password</label>
          <input type="password" id="inputConfirmPassword" className="form-control" placeholder="Confirm Password" required />
          <div class="valid-feedback"></div>
          <div class="invalid-feedback">Please fill out this field.</div>
          </div>
          <button className="btn btn-primary" type="submit" style={button} onClick={register}>Register</button>
        </div>
      </div>
    )
}

export default register;