import React,{ useRef,useState,useEffect } from 'react';
import AddPost from './addpost';
import AddCategory from './category';

import $ from 'jquery';

const dashboard = function Dashboard() {
  const [addPost,setAddPost] = useState(false);
  const [addCategory,setAddCategory] = useState(false);

  const bgstyle={
    backgroundColor: '#282c34',
    color: 'white'
  }

  const sidebar={
    height: '150px',
    left: '0',
    top: '145px',
    paddingRight: '58px',
    position: 'absolute',
    backgroundColor: 'lightblue',
    color: 'black'
  }

  const collapse={
    height: '50px',
    left: '0',
    top: '95px',
    paddingRight: '30px',
    marginLeft: '-30px',
    position: 'absolute',
    backgroundColor: 'lightblue',
    color: 'black',
    width: '193px'
  }

  const ulnobullet={
    listStyleType: 'none',
    left: '0'
  }

  useEffect(() => {
    $(".container-fluid").css("position","fixed");
  })

  return (
      <div className="container" style={bgstyle}>
        <h2>This is Dashboard Page</h2>
        <div  style={collapse}>
        <div className="row">
            <ul className="mx-auto" style={ulnobullet}>
              <li className="nav-item">
                  <a className="nav-link" href="#/dashboard"  data-toggle="collapse" data-target="#sidebar">Menu</a>
              </li>
            </ul>
          </div>
        </div>
        <div id="sidebar" className="collapse" style={sidebar}>
          <div className="row">
            <ul className="mx-auto" style={ulnobullet}>
              <li className="nav-item">
                  <a className="nav-link" href="#/dashboard">Home</a>
              </li>
              <li className="nav-item">
                  <a className="nav-link" href="#/dashboard" onClick={()=>{setAddPost(true);setAddCategory(false);}}>Posts</a>
              </li>
              <li className="nav-item">
                  <a className="nav-link" href="#/dashboard" onClick={()=>{setAddPost(false);setAddCategory(true);}}>Category</a>
              </li>
            </ul>
          </div>
        </div>
        <div className="content">
          {addPost && (<AddPost />)}
          {addCategory && (<AddCategory />)}
        </div>
      </div>
    );
}

export default dashboard;