import React,{ useRef,useState,useEffect } from 'react';

import firebase from './../../firebase.js';
import $ from 'jquery';
import moment from 'moment';

const login = function Login() {
  const email = useRef(null);
  const password = useRef(null);
  const [countLogs,setcountLogs] = useState(0);
  const [myip,setmyip] = useState('');
  const [mylocation,setmylocation] = useState('');
  const [errorlogin,seterrorlogin] = useState('');

  const bgstyle={
    backgroundColor: '#282c34',
    color: 'white'
  }

  const loginform={
    borderRadius: '25px',
    backgroundColor: 'lightblue'
  }

  const button={
    borderRadius: '15px',
    margin: '0 0 15px 0',
  }

  const fontcolor={
    color: 'blue'
  }

  useEffect(() => {
    //$("div.container").css({display:'none'});
    firebase.database().ref("/logs").on('value', snapshot => {
      var select_logs = [];
      snapshot.forEach(child => {
        var data = child.val();
        select_logs.push({
            id: data.id,
            status: data.status,
            message: data.message,
            agent: data.agent,
            ip: data.ip,
            location: data.location,
            user_id: data.user_id,
            time: data.time,
          });
      //this.setState({ count_logs: select_logs.length })
      setcountLogs(select_logs.length);
      
      fetch('http://ip-api.com/json')
      .then(res => res.json())
      .then((data) => {
        setTimeout(function(){ 
          //this.setState({ myip: data.query, mylocation: data.country+'#'+data.city })
          setmyip(data.query)
          setmylocation(data.country+'#'+data.city)
        }, 300);
      });
      
      });
    });
    $(".container-fluid").css("position","fixed");
  })

  function login() {

      const auth = firebase.auth();
      try{
        auth.signInWithEmailAndPassword(email.current.value,password.current.value)
          .then(() => { userauth_login(email.current.value);window.location.reload(false); })
          .catch(error => {   
            //alert(error.code)
            //setState({ errorlogin: 'Invalid user or password' });
            seterrorlogin('Invalid user or password');
            setTimeout(function(){ 
              //setState({ errorlogin: '',email: '',password: '' });
              seterrorlogin('');
              email.current.value = '';
              password.current.value = '';
            }, 2000);
            switch(error.code) {
              case 'auth/user-not-found':
                    alert('Incorect User or Password !');
                    break;
              case 'auth/wrong-password':
                alert('Incorect User or Password !');
                break;
              default:
                break;
          }
        });
      }catch(err){
          alert("Error : ", err);
      }
      
  }

  function userauth_login(id){
          localStorage.setItem('authlogin', 'true');
          localStorage.setItem('user_id', id);
          //window.location.reload();
          //return <Redirect to="/reactmaterial/"/>;
          //alert('logout!'+ localStorage.getItem('authlogin'));
          window.location.href = "#/dashboard";

          var curtime = moment().format("DD/MM/YYYY HH:mm:ss");
          //alert("Login:"+countLogs+myip+id);
          var refdb = firebase.database().ref('/logs').push();
          refdb = refdb.toString().slice(-20);
          //setTimeout(function(){ 
          let updateData = firebase.database().ref("/logs").child(refdb);
          updateData.child("id").set(countLogs+1);
          updateData.child("status").set('1');
          updateData.child("message").set('User Login');
          updateData.child("agent").set(navigator.userAgent);
          updateData.child("ip").set(myip);
          updateData.child("location").set(mylocation);
          updateData.child("user_id").set(id);
          updateData.child("time").set(curtime);
          //}, 300);
          /*setTimeout(function(){ 
            firebase.database().ref('/logs').set({
              id: countLogs+1,
              status: 1,
              message: 'User Login',
              agent: navigator.userAgent,
              ip: myip,
              location: mylocation,
              user_id: id,
              time: curtime
            });
          }, 300);*/
  }

  return (
      <div className="container" style={bgstyle}>
        <h2>This is Login Page</h2>
        <div className="card col-6 was-validated mx-auto" style={loginform}>
          <h1 className="h3 mb-3 font-weight-normal" style={fontcolor}>Please sign in</h1>
          <div className="form-group">
          <label htmlFor="inputEmail" className="sr-only">Email address</label>
          <input ref={email} type="email" id="inputEmail" className="form-control" placeholder="Email address" required autoFocus />
          <div className="valid-feedback"></div>
          <div className="invalid-feedback">Please fill out this field.</div>
          </div>
          <div className="form-group">
          <label htmlFor="inputPassword" className="sr-only">Password</label>
          <input ref={password} type="password" id="inputPassword" className="form-control" placeholder="Password" required />
          <div className="valid-feedback"></div>
          <div className="invalid-feedback">Please fill out this field.</div>
          </div>
          <div className="form-group">
          <div className="checkbox mb-3">
              <label>
                <input type="checkbox" value="remember-me" style={loginform}/> Remember me
              </label>
          </div>
          </div>
          <button className="btn btn-primary" type="submit" style={button} onClick={login}>Login</button>
        </div>
      </div>
    );
}

export default login;