import React,{ Component } from 'react';
import firebase from './../../firebase';

export default class Fileupload extends Component {
  constructor(props) {
      super(props);
      this.state = {
        image: null
      }
    }
  
  handleChange=(e)=>{
    if(e.target.files[0]){
      //setImage(e.target.files[0]);
      this.setState({ 
        image: e.target.files[0]
      });
    }
  }

  handleUpload(){
    const uploadTask = firebase.storage().ref('image/'+this.image.name).put(this.image);
    uploadTask.on(
      "state_changed",
      snapshot => {},
      error => {
        console.log(error);
      },
      ()=>{
        firebase.storage().ref("images").child(this.image.name).getDownloadURL().then(url =>{localStorage.setItem("image_url",this.image.name);});
        alert("Image was uploaded!");
      });
  }

  render(){
    const bgstyle={
      backgroundColor: '#282c34',
      color: 'white'
    }

  return (
      <div className="container" style={bgstyle}>
        <h2>This is Fileupload Page</h2>
        <br/>
        <input type="file" onChange={()=>this.handleChange} />
        <button className="btn btn-primary" onClick={()=>this.handleUpload}>Upload</button>
      </div>
    );
  }
}